import React, { useState } from   'react';
import { ProgressBar } from "react-bootstrap";
import { useNavigate } from "react-router-dom";

import "../css/Main.css";


const Main = () => {
    const navigate = useNavigate();
    const [todo, setTodo] = useState({});


    function addTodo(){
        console.log("add button");
    }

    const handleOnInput = (e)=>{
        setTodo(e.target.value);
    }

    function handleOnKeyPress(e){
        if(e.key === "Enter"){
            addTodo();
        }
    }

    function toHome(){
        navigate("/");
    }


    return (
        <>
            <div className="main_top">
                <div className="main_top_todo">
                    <div>Miracle Morning</div>
                </div>
                <div className="main_top_day">
                    <div>D-37</div>
                </div>
            </div>
            <div className="main_content">
                <div  className="main_content_level">
                    1 Level
                </div>

                <div className="expBar">
                    <ProgressBar style={{width: "800px", height:"30px", backgroundColor:"white"}} animated={true} now={60} variant="barColor"/>
                </div>

                <div className="main_content_input">
                    <div className="main_content_input_title">Todo</div>
                    <div  className="main_content_input_box">
                        <input type="text"  placeholder="Write here essential milestones for your goal"  onKeyPress={handleOnKeyPress}/>
                        <button onClick={addTodo}>add</button>
                    </div>
                </div>  
                <div className="main_content_toDoBox">
                
                </div>
                <div className="main_btn" onClick={toHome} >
                    SATISFIED?
                </div>
            </div>
        </>

    )
}

export default Main;